import barba from '@barba/core'

import { resetWebflow, restartBackgroundVideos, initPage } from '../main'
import { initScroll } from '../utils/scroll.js' // Adjust the path as needed
import { gsap, ScrollTrigger, SplitText } from './gsapInit'

// Reusable function to re-initialize page-specific scripts
function reinitializePage(data) {
  resetWebflow(data) // Reset and reinitialize Webflow interactions
  initPage() // Initialize page-specific scripts
  initScroll() // Initialize custom scroll behaviors
  //restartBackgroundVideos() // Restart any background videos
  window.scrollTo(0, 0) // Scroll to the top of the page
  ScrollTrigger.refresh() // Refresh ScrollTrigger instances if any
}
function reinitializePageAndVideos(data) {
  resetWebflow(data) // Reset and reinitialize Webflow interactions
  initPage() // Initialize page-specific scripts
  initScroll() // Initialize custom scroll behaviors
  restartBackgroundVideos() // Restart any background videos
  window.scrollTo(0, 0) // Scroll to the top of the page
  ScrollTrigger.refresh() // Refresh ScrollTrigger instances if any
}

export default function initPageTransitions() {
  barba.init({
    debug: false, // Enable debugging to see detailed logs
    preventRunning: true, // Prevent concurrent transitions
    transitions: [
      {
        sync: false,
        name: 'fade',
        once(data) {
          const tl = gsap.timeline({
            onComplete: () => {
              reinitializePage(data)
              //console.log('Initial load animation completed')
            },
          })

          // Ensure .transition-wrapper is initially shown
          tl.set('.transition-wrapper', {
            display: 'block',
          })

          // Set initial state for .transition-image to be invisible
          tl.set('.transition-image', {
            opacity: 0,
          })

          // Set initial state for .transition_background to be alpha 1
          tl.set('.transition_background', {
            opacity: 1,
          })

          // Animate .transition-image to fade in
          tl.to('.transition-image', {
            opacity: 1, // Start from invisible
            duration: 0.3,
            ease: 'power1.out',
          })

          // Animate the disappearance of all elements
          tl.to(['.transition-image', '.transition_background-column'], {
            autoAlpha: 0,
            stagger: 0.05, // Add a delay between the disappearance of each element
            duration: 0.1,
            ease: 'power1.out',
          })

          // Finally hide the .transition-wrapper at the end of the animation
          tl.to('.transition-wrapper', {
            display: 'none',
          })

          return tl
        },

        leave(data) {
          const tl = gsap.timeline({
            //onStart: () => console.log('Starting LEAVE transition'),
            //onComplete: () => {
            //console.log('LEAVE TRANSITION END')
            //},
          })
          // Ensure .transition-wrapper is initially shown
          tl.set('.transition-wrapper', {
            display: 'block',
          })
          // Animate the elements
          tl.to('.transition_bacground-column', {
            autoAlpha: 1,
            stagger: 0.05,
            duration: 0.1,
            ease: 'power1.out',
          })
          // Animate the elements
          tl.to('.transition-image', {
            autoAlpha: 1,
            duration: 0.1,
            ease: 'power1.out',
          })
          return tl
        },
        after(data) {
          const tl = gsap.timeline({
            //onStart: () => console.log('Starting AFTER transition'),
            onComplete: () => {
              reinitializePageAndVideos(data) // Reinitialize the page after the enter transition
              //console.log('AFTER TRANSITION END')
            },
          })
          // Animate the elements
          tl.to('.transition-image', {
            opacity: 0,
            duration: 0.1,
            ease: 'power1.out',
          })
          // Animate the elements
          tl.to('.transition_bacground-column', {
            opacity: 0,
            duration: 0.1,
            stagger: 0.05,
            ease: 'power1.out',
          })
          // Ensure .transition-wrapper is hidden
          tl.set('.transition-wrapper', {
            display: 'none',
          })
          return tl
        },
      },
    ],
    views: [
      {
        namespace: 'home',
        enter(data) {
          // Homepage specific code
          //console.log('after home animazione da namespace')
        },
      },
      {
        namespace: 'contatti',
        beforeEnter(data) {
          // Contact page specific code
          //console.log('before enter struttura animazione da namespace')
        },
      },
      // Additional namespaces for other pages
    ],
  })
}
