import { animationSettings } from './animationVars'
import { gsap, ScrollTrigger } from './gsapInit'

// Register ScrollTrigger with GSAP
gsap.registerPlugin(ScrollTrigger)

// Debounce function to limit the rate of function execution
const debounce = (func, wait) => {
  let timeout
  return (...args) => {
    clearTimeout(timeout)
    timeout = setTimeout(() => func.apply(this, args), wait)
  }
}

const initParallax = () => {
  // Query all elements with the data-scroll attribute set to true
  const parallaxElements = document.querySelectorAll('[data-scroll="true"]')

  const updateParallax = () => {
    const viewportHeight = window.innerHeight

    parallaxElements.forEach((element) => {
      const speed = parseFloat(element.getAttribute('data-scroll-speed')) || 1
      const normalizedSpeed = speed * (viewportHeight / 600) // Adjust this factor as needed

      // Apply the parallax effect using GSAP
      gsap.to(element, {
        y: () => `+=${-100 * normalizedSpeed}`,
        ease: animationSettings.easingOptions.primary,
        scrollTrigger: {
          trigger: element,
          start: 'top bottom',
          end: 'bottom top',
          toggleActions: 'play none none reverse',
          scrub: true,
        },
      })
    })
  }

  // Initial update
  updateParallax()

  // Setup a ResizeObserver to refresh ScrollTrigger instances on resize
  const resizeObserver = new ResizeObserver(() => {
    ScrollTrigger.refresh(true)
  })

  // Observe each parallax element for size changes
  parallaxElements.forEach((element) => {
    resizeObserver.observe(element)
  })
}

// Ensure ScrollTrigger is properly refreshed on window load and resize
window.addEventListener('load', () => {
  ScrollTrigger.refresh(true)
})

window.addEventListener(
  'resize',
  debounce(() => {
    ScrollTrigger.refresh(true)
    // Instead of reinitializing, just refresh the ScrollTrigger instances
    ScrollTrigger.refresh()
  }, 150)
)

export default initParallax
