import { animationSettings } from './animationVars'
import { gsap, ScrollTrigger, SplitText } from './gsapInit'

const initMediaEffects = () => {
  // Query all image and video elements with specific effect classes
  document
    .querySelectorAll('img[class*="img-"], video[class*="video-"]')
    .forEach((media) => {
      const effectMatch = media.className.match(/(img|video)-(\w+)/)
      if (effectMatch) {
        const [, mediaType, effect] = effectMatch
        applyMediaAnimation(media, effect)
      } else {
        console.warn('No valid effect class found on media:', media)
      }
    })
}

const applyMediaAnimation = (media, effect) => {
  const tl = gsap.timeline({
    scrollTrigger: {
      trigger: media,
      start: 'top 90%',
      end: 'bottom 10%',
      toggleActions: 'play none none reverse',
      scrub: true,
    },
  })

  const defaultDuration = 0.6
  const defaultEasing = 'power1.out'

  const animationOptions = {
    duration:
      animationSettings.durationOptions[animationSettings.selectedDuration] ||
      defaultDuration,
    ease:
      animationSettings.easingOptions[animationSettings.selectedEasing] ||
      defaultEasing,
  }

  // Define animation effects
  const effects = {
    fadein: { from: { opacity: 0 }, to: { opacity: 1 } },
    fadeup: { to: { y: 50 } },
    slidein: { from: { x: -100 } },
    zoom: { from: { scale: 1.1 } },
    rotatein: { from: { rotation: 90 } },
  }

  if (effects[effect]) {
    const { from = {}, to = {} } = effects[effect]
    tl.fromTo(
      media,
      { ...from, ...animationOptions },
      { ...to, ...animationOptions }
    )
  } else {
    console.warn(`No animation defined for effect: ${effect}`)
  }
}

export default initMediaEffects
