import Lenis from 'lenis'

import { gsap, ScrollTrigger } from '../utils/gsapInit'

const navbarAnimation = () => {
  const navbar = document.querySelector('nav.navbar_menu')
  const topBar = document.querySelector('.navbar_top-bar')
  if (!navbar || !topBar) {
    console.error('Navigation or top-bar element not found')
    return
  }

  const lenis = new Lenis({
    smooth: true,
    smoothWheel: true,
  })

  const updateBrightness = (isDarkSection) => {
    const brightnessValue = isDarkSection ? '200%' : '0%'
    gsap.to(topBar, { filter: `brightness(${brightnessValue})`, duration: 0.5 })
  }

  const logNavbarState = () => {
    const hasDataNavMenuOpen = navbar.hasAttribute('data-nav-menu-open')
    if (hasDataNavMenuOpen) {
      console.log('Navbar open')
      lenis.stop() // Stop Lenis scrolling
      document.body.classList.add('no-scroll') // Add class to hide scrollbar and prevent scrolling
      gsap.to(topBar, { filter: 'brightness(200%)', duration: 0.5 })
    } else {
      console.log('Navbar closed')
      lenis.start() // Start Lenis scrolling
      document.body.classList.remove('no-scroll') // Remove class to allow scrolling
      ScrollTrigger.refresh() // Refresh ScrollTrigger to apply brightness based on sections
    }
  }

  const observer = new MutationObserver((mutations) => {
    mutations.forEach((mutation) => {
      if (mutation.attributeName === 'data-nav-menu-open') {
        logNavbarState()
      }
    })
  })

  observer.observe(navbar, {
    attributes: true,
    attributeFilter: ['data-nav-menu-open'],
  })

  // Initialize ScrollTrigger to detect section changes
  const sections = document.querySelectorAll('section, header')
  sections.forEach((section) => {
    ScrollTrigger.create({
      trigger: section,
      start: 'top center',
      end: 'bottom center',
      onEnter: () =>
        updateBrightness(section.classList.contains('dark-section')),
      onEnterBack: () =>
        updateBrightness(section.classList.contains('dark-section')),
    })
  })

  // Initial check
  logNavbarState()
  ScrollTrigger.refresh()
}

export default navbarAnimation
