import { animationSettings } from './animationVars'
import { gsap, ScrollTrigger, SplitText } from './gsapInit'

const enableRevert = false // Set true to revert animations when leaving the viewport

// Object to store different animation effects
const animationEffects = {
  slideup: (tl, elements, options) => tl.from(elements, { y: 50, ...options }),
  slidein: (tl, elements, options) => tl.from(elements, { x: -50, ...options }),
  rotatein: (tl, elements, options) =>
    tl.from(elements, { rotation: 360, ...options }),
  popin: (tl, elements, options) =>
    tl.from(elements, { scale: 0.5, ease: 'back.out(1.7)', ...options }),
  fadein: (tl, elements, options) => tl.from(elements, { ...options }),
  fadeup: (tl, elements, options) =>
    tl.from(elements, { y: 50, opacity: 0, ...options }),
  progress: (tl, elements, options) =>
    tl.from(elements, { opacity: 0.1, ...options }),
}

// Mapping of target units to SplitText types
const targetUnits = {
  l: 'lines',
  w: 'words',
  c: 'chars',
}

// Function to add CSS styles dynamically
const addStyles = () => {
  const style = document.createElement('style')
  style.innerHTML = `
    .split-parent {
      overflow: hidden;
    }
    .split-word, .split-char {
      display: inline-block;
    }
  `
  document.head.appendChild(style)
}

// Function to initialize text effects on elements
const initTextEffects = () => {
  addStyles()

  // Apply overflow hidden to elements with specific classes
  document
    .querySelectorAll('.txt-fadeup-w-h, .txt-fadeup-c-h, .txt-fadeup-l-h')
    .forEach((el) => {
      el.style.overflow = 'hidden'
    })

  // Iterate through each element with a specific effect class
  document.querySelectorAll('[class*="txt-"]').forEach((el) => {
    const match = el.className.match(/txt-(\w+)-([lwc])(-[hv])?/)
    if (match) {
      const effect = match[1]
      const targetUnit = match[2]

      const split = new SplitText(el, {
        type: targetUnits[targetUnit],
        linesClass: 'split-line',
        wordsClass: 'split-word',
        charsClass: 'split-char',
      })

      applyAnimation(el, effect, targetUnit, split)
    } else {
      console.warn('No valid effect class found on element:', el)
    }
  })
}

// Function to apply animation based on effect and target unit
const applyAnimation = (element, effect, targetUnit, split) => {
  const targetElements = split[targetUnits[targetUnit]]

  if (!targetElements || targetElements.length === 0) {
    console.warn(
      `No elements found for animation: ${effect}, target: ${targetUnit}`
    )
    return
  }

  const tl = gsap.timeline({
    scrollTrigger: {
      markers: false,
      trigger: element,
      start: 'top 70%',
      end: 'bottom 20%',
      toggleActions: 'play none none reverse',
      scrub: false,
    },
    onComplete: () => {
      if (enableRevert) {
        split.revert()
      }
    },
  })

  const animationOptions = {
    duration:
      animationSettings.durationOptions[animationSettings.selectedDuration] ||
      0.8,
    stagger: 0.05,
    ease:
      animationSettings.easingOptions[animationSettings.selectedEasing] ||
      'power1.out',
  }

  const animationFunction = animationEffects[effect.toLowerCase()]

  if (animationFunction) {
    animationFunction(tl, targetElements, animationOptions)
  } else {
    console.warn(`No animation defined for effect: ${effect}`)
  }
}

export default initTextEffects
