import Lenis from 'lenis'

import { animationSettings } from './animationVars'
import { gsap, ScrollTrigger, SplitText } from './gsapInit'

const initScroll = () => {
  const lenis = new Lenis()

  lenis.on('scroll', (e) => {
    //console.log(e)
  })

  lenis.on('scroll', () => {
    ScrollTrigger.refresh() // Refresh ScrollTrigger when scrolling occurs
  })

  gsap.ticker.add((time) => {
    lenis.raf(time * 1000)
  })

  gsap.ticker.lagSmoothing(0)
}

export { initScroll } // Export initScroll function
true
